import uniList from "../../components/uni-list/uni-list.vue"
import uniListItem from "../../components/uni-list-item/uni-list-item.vue"
export default {
	components: {uniList,uniListItem},
	data() {
		return {
			myAddress:'',	//地址
			detailed:'',	//地址详情
			mobile:'',		//手机号
			name:'',		//姓名
			USERINFO:[],
			latitude:'',
			longitude:''
			
		}
	},
	onLoad:function(Option){
		this.USERINFO = this.checkLogin();//判断用户是否登陆
		this.initAddress();//初始化地址信息
		this.doLocation();

	},
	onBackPress:function(Options){
		
	},
	methods: {
		formSubmit:function(e){
			e.detail.value.user_id = uni.getStorageSync('USERID')
			 console.log(e.detail.value.user_id);
			var formdata = e.detail.value;
			uni.request({
				url:this.apiServer+"/api/address/save",
				method:'POST',
				data:JSON.stringify(formdata),
				success: (res) => {
					console.log(res);
					if(res.data.err==0){
						uni.showModal({
							title:'提示',
							content:'地址变更成功',
							showCancel:false
						})
					}else{
						uni.showModal({
							title:'提示',
							content:'地址变更失败',
							showCancel:false
						})
					}
				}
			})
			uni.navigateBack({
			    delta: 1,
			});
		},

		initAddress:function(){
			uni.request({
				url: this.apiServer+'/api/address/index?user_id='+uni.getStorageSync('USERID'),
				method: 'GET',
				success: res => {
					var data = res.data;
					if(data.err==1){
						uni.showModal({
							title:'错误提示',
							content:'请完善您的服务地址',
							showCancel:false
							
						});
					}else{
						console.log(data.data)
						this.myAddress = data.data.address;
						this.name = data.data.name;
						this.mobile = data.data.mobile;
						this.detailed = data.data.detailed;
					}
				},
			});
		},
		
		doLocation:function(){
			var tt = this
			uni.getLocation({
			    success: function (res) {
					tt.address = res.address + res.name;
					tt.latitude = res.latitude;
					tt.longitude = res.longitude;
			    }
			});
		}
	}
}
